  format_Date(date) {
    date = moment(date).toDate();
    var monthNames = [
      "January",
      "Fvrier",
      "Mars",
      "Avril",
      "Mai",
      "Juin",
      "Juillet",
      "Aot",
      "Septembre",
      "Octobre",
      "Novembre",
      "Dcembre"
    ];

    var day = date.getDate();
    var monthIndex = date.getMonth();
    var trans = monthNames[monthIndex];
    return day + "-" + trans;
  }

