  getduserbookingfordemande(date){
    this.spinner.show();
    var coachid = JSON.parse(localStorage.getItem("Coach"));
    var data = {
      Coach_ID: coachid.Coach_Id,
      Date:date
    };
    this.bookedCoachSide = [];
    this.appService
      .getAll("/coach/getdemandavailabilityByCoach", data)
      .subscribe((data) => {
        this.spinner.hide();
        if ((data as any).data.availabilty.length > 0) {
          this.bookedCoachSide = (data as any).data.availabilty;
        }else{
          this.bookedCoachSide = [];
        }
      });

  }

  mapintigration(mappoint, cityname,address) {
  const BASE_NOMINATIM_URL: string = 'nominatim.openstreetmap.org';
  const DEFAULT_VIEW_BOX: string = 'viewbox=-25.0000%2C70.0000%2C50.0000%2C40.0000';
  let url = `https://${BASE_NOMINATIM_URL}/search?format=json&q=${address}&${DEFAULT_VIEW_BOX}&bounded=1`;
  this.appService.get(url).subscribe((response:any) => {
     console.log(response,'response----Addresss')
     var coordonnees = [];
     if(response.length > 0){
      coordonnees.push(response[0].lat);
      coordonnees.push(response[0].lon);
      this.lat = Number(response[0].lat).toFixed(3);
      this.lang = Number(response[0].lon).toFixed(3);
      this.generatedMap(coordonnees,cityname,address)
     }else{
      this.generatedMap(mappoint,cityname,'');
     }
   },error =>{
    this.generatedMap(mappoint,cityname,'');
  });
  }

  generatedMap(mappoint,cityname,address){
    this.map = L.map("map", {
      center: mappoint,
      zoom: 16,
    });

    const tiles = L.tileLayer(
      "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      {
        maxZoom: 25,
      }
    );

    tiles.addTo(this.map);
    var greenIcon = L.icon({
      iconUrl: "../assets/images/marker-icon.png",
    });
    L.marker(mappoint, { icon: greenIcon }).addTo(this.map).bindPopup(address ? address : cityname).openPopup();
  }
  mapRerender(mappoint, cityname,address) { 

    this.map.remove();
    this.mapintigration(mappoint, cityname,address);

  }

  private refresh(): boolean {
    return this.searchQuery && this.searchQuery === this.previousSearchQuery;
  }

  public doRefresh(): void {
    this.routedComponent.refresh();
  }

  async getcurrentcordinates() {
    const resp = await fetch("https://ipapi.co/json/");
    const data = await resp.json();
    this.curentlat = data.latitude.toFixed(3);
    this.curentlang = data.longitude.toFixed(3);
  }

  mapurl;
  villeSelected(postal_code: string) {
    this.selectedCity = postal_code;
    this.spinner.show();
    this.appService.getAll("/city/getcitybyID/" + postal_code).subscribe((response) => {
      this.spinner.hide();
      if (response && response["data"]) {
        console.log(response);
        var dat = (response as any).data.city_list[0];
        this.mapvalues = eval("[" + dat.coordonnees_gps + "]");
        this.lat = this.mapvalues[0].toFixed(3);
        this.lang = this.mapvalues[1].toFixed(3);
        this.mapurl = `https://www.openstreetmap.org/directions?engine=fossgis_osrm_car&route=${this.curentlat}%2C${this.curentlang}%3B${this.lat}%2C${this.lang}`;
        this.mapRerender(this.mapvalues, dat.Libelle_acheminement,this.courseAddress);
      }
    });
  }
  
