  formatDate(date) {
    var d = new Date(date),
      month = "" + (d.getMonth() + 1),
      day = "" + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;

    return [year, month, day].join("-");
  }

  bookCoach() {
    var req = {
      bookArray: this.bookArray,
      totalAmt: this.booking.amount,
      remaingStatus: this.is10Hr == true ? "Yes" : "No",
    };

    this.spinner.show();
    this.appService
      .create("/coach/setreservationfun", req)
      .subscribe((response) => {
        if (response && response.isSuccess == true) {
          $(".btnbookingCoursIndividual").hide();
          this._showAlertMessage("alert-success", "Cours rserv avec succs");
          setTimeout(function () {
            $("#available").hide();
          }, 3000);
        } else {
          this._showAlertMessage(
            "alert-danger",
            "La rservation du cours a chou"
          );
        }
        this.spinner.hide();
      });
  }

